body{
    font-family: Arial, Helvetica, sans-serif;
    background-color: brown;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    padding: 0;
}

.container{
    background-color: aqua;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgb(56, 55, 55);
    width: 100%;
    max-width: 400px;
}

h2{
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
}

label{
    display: block;
}

button{
    background-color: blue;
    color: white;
    margin-top: 20px;
    
}
